:root {
  --primary: #40607a;
}

.btn2 {
  margin-top: 400px;
  margin-left: 60px;
  padding: 10px 30px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  background-color: var(--primary);
}

.btn2:hover {
  padding: 8px 28px;
  transition: all 0.3s ease-out;
  background-color: transparent;
  color: #fff;
  border-radius: 4px;
  border: 2px solid var(--primary);
  transition: all 0.3s ease-out;
}
